<template>
    <div>
        <!-- 头部 -->
        <van-nav-bar title="商品详情" fixed left-arrow @click-left="onClickLeft"
            style="border-bottom: 0.02667rem solid #dcdcdc;margin-bottom: 10px;">
            <template #right>
                <van-icon name="ellipsis" size="18"  />
            </template>
        </van-nav-bar>
        <!-- 商品图片 -->
        <van-image :src="goods.goodsCoverImg">
            <template v-slot:error>加载失败</template>
        </van-image>
        <p class="goodsName">{{ goods.goodsName }}</p>
        <p class="express">免邮费 顺丰快递</p>
        <p class="price">￥{{ goods.sellingPrice }}</p>
        <!-- <p class="serve">概述|参数|安装服务|常见问题</p> -->
        <ul class="serve">
            <li style='border-right: 1px solid;'>概述</li>
            <li style='border-right: 1px solid;'>参数</li>
            <li style='border-right: 1px solid;'>安装服务</li>
            <li>常见问题</li>
        </ul>
        <div v-html="goods.goodsDetailContent" class="content">
        </div>
        <!-- 商品 -->
        <!-- 底部 -->
        <van-goods-action>

            <van-goods-action-icon icon="chat-o" text="客服" />
            <van-goods-action-icon icon="cart-o" text="购物车" to='/cart' />
            <van-icon :badge="$store.state.cartList.length" v-show="$store.state.cartList.length > 0" />
            <van-goods-action-button color="#be99ff" type="warning" text="加入购物车" @click="add" />
            <van-goods-action-button color="#7232dd" type="danger" text="立即购买" />
        </van-goods-action>

    </div>
</template>

<script>
// 导入添加购物车接口
import { addCart, goods } from "@/api/index"
import { Toast } from "vant";
export default {
    name: "info",
    props: [],
    mounted() {
        // 详情页接口函数接收数据
        this.getData()
        // this.$store.dispatch("changeCartListAsync") //进来就获取购物车数据
    },
    data() {
        return {
            message: 0, //客服信息数量
            cart: 0,    //购物车数量
            goods: {}   //接收的数据
        }
    },
    methods: {
        // 点击头部左上角的<
        onClickLeft() {
            this.$router.back()
        },

        getData() {
            // 调用详情页商品接口
            goods(this.$route.params.productId).then(data => {
                if (data.resultCode === 200) {
                    // console.log(data);
                    this.goods = data.data
                }
            })
        },
        // 点击加入购物车
        add() {

            // 调用添加购物车接口
            addCart({
                goodsId: this.goods.goodsId,
                goodsCount: 1
            }).then(data => {
                // console.log(data);
                if (data.resultCode == 200) {
                    Toast.success("添加成功")
                    // 调用仓库store中action的方法
                    this.$store.dispatch("changeCartListAsync")
                } else {
                    Toast.fail("商品已存在")
                }
            })
        }
    }
}
</script>

<style lang="less" scoped>
/deep/.van-nav-bar__arrow {
    color: #252525;
    font-weight: 500;
}

.van-icon-ellipsis {
    color: #252525;
    font-weight: 500;
}

.van-image {
    margin-top: 46px;
}

.goodsName {
    font-size: 18px;
    color: #333;
    text-align: left;
    padding: 2px 10px;
}

.express {
    font-size: .37333rem;
    text-align: left;
    color: #999;
    padding: 0.13333rem 10px;
}

.price {
    color: #f63515;
    font-size: .58667rem
}

.serve {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    margin: 15px 0;

    li {
        flex: 4;
        text-align: center;

    }
}

.content {
    width: 100%;
}

.van-goods-action {
    position: fixed;
}

.van-info {
    position: absolute;
    top: -15px;
    right: 15px;
}
</style>